/*google fonts roboto*/
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

body {
    background-color: #000;
    color: #fff;
}

/*цвета*/
.--color-gray {
    color: #979797; 
}

.--color-white {
    color: #fff;
}

.--color-turquoise {
    color: #55D6C6;
}

/*цвета*/

/*шрифты*/
.--roboto-r14 {
    font-family: 'Roboto';
    font-size: 18px;
    font-weight: 400;
}

.--tt-b30 {
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    text-align: center;
    text-transform: uppercase;
}

.--tt-b50 {
    font-family: 'TT Norms';
    font-size: 50px;
    font-weight: 700;
    
}

.--tt-b25 {
    font-family: 'TT Norms';
    font-style: normal;
    font-weight: 700;
    font-size: 25px;
    text-align: center;
}

.--tt-m20 {
    font-family: 'TT Norms';
    font-size: 20px;
    font-weight: 500;
}

.--tt-r50 {
    font-family: 'TT Norms';
    font-size: 50px;
    font-weight: 400;
   
}

.--tt-l20 {
    font-family: 'TT Norms';
    font-size: 20px;
    font-weight: 300;
}




/*шрифты*/

/*общие стили*/
.container {
    width: 1160px;
    margin: 0 auto;
    padding: 15px;
}

.menu__list, .social__list {
    list-style: none;
}

.logo, .menu__list-link,  .social__list-link {
    text-decoration: none;
}

.social__list-item {
    margin-left: 25px;
}

/*общие стили*/

/*style for section info*/
.info {
    margin-top: 92px;
}

.info__block {
    margin-top: 15px;
    background-image: url('../img/bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    
}

.--text-white {
    margin-bottom: 25px;
}

.info__block-text {
    margin-top: 10px;
}


/*style for section info*/

/*style for section foto*/
.foto {
    margin-top: 20px;
}

.foto__image {
    margin: 10px auto;
    display: block;
    width: 799px;
    height: 447px;
    border-radius: 20px;
}

/*style for section foto*/

/*style for section order*/
.order {
    background-image: url('../img/bg.png');
    background-repeat:no-repeat;
    background-size: cover;
    margin-top: 56px;

}

.form-order {
    margin-top: 40px;
}

.phone, .name {
    width: 323px;
    height: 76px;
    border-radius: 15px;
    padding: 15px;
    margin-left: 18px;
}

.btn-order {
    background: linear-gradient(180deg, #79DDCF 0%, #05A696 100%);
    border: none;
    border-radius: 15px;
    padding: 15px;
    width: 323px;
    height: 76px;
    margin-left: 18px;
    margin-top: 18px;

}

.checkbox {
    margin-left: 18px;
}

.label {
    width: 289px;
}

/*style for section order*/
